* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #262626;
}

a {
  text-decoration: none;
  color: #262626;
}

button {
  cursor: pointer;
}

li {
  list-style-type: none;
}

html {
  font-size: 15px;
  font-family: "Commissioner", Helvetica, Arial, sans-serif;
  color: #262626;
}

body {
  background-color: #eefdfe;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  width: 100vw;
  height: 54px;
  margin-bottom: 2px;
  padding: 0 5%;
}

.mobile__logo > a {
  display: block;
  height: 49px;
  width: 120px;
}

.header__logo a {
  display: inline-block;
  height: 54px;
  padding: 16px 6px 10px;
  margin-right: 15px;
}

.header__logo a:focus {
  border: 1px dashed black;
}

.header__logo--img {
  width: 108px;
  height: 23px;
}

.header__left {
  display: flex;
  align-items: center;
  width: 70%;
}

.header__actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  font-size: 13px;
  cursor: pointer;
}

.header__actions button {
  background: #fff;
  border: 0;
}

.header__surface {
  width: 95px;
}

.header__mobile {
  display: none;
  height: 49px;
  background-color: #fff;
  width: 100%;
}

.header__mobile--top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 49px;
}

.hover {
  position: relative;
}

.hover:before,
.hover::after {
  content: "";
  position: absolute;
  bottom: -4px;
  width: 0;
  height: 2px;
  opacity: 0;
  transition: all 0.2s ease-in;
  background-color: #262626;
  left: 0;
}

.hover:hover {
  cursor: pointer;
}

.hover:hover::after,
.hover:hover:before {
  width: 100%;
  opacity: 1;
}

.check {
  display: inline-block;
  color: #000;
  text-transform: uppercase;
  font-family: sans-serif;
  font-size: 8px;
}

.check:after {
  content: "⌵";
  display: block;
  text-align: center;
  font-size: 155%;
  font-weight: bold;
}

.surface {
  font-size: 18px;
  font-weight: 600;
  width: 95px;
  display: flex;
  justify-content: space-evenly;
}

.surface > span {
  margin-top: -1px;
}

.surface::before {
  content: "";
  border-left: 2px solid black;
  height: 25px;
}

.mobile__check {
  display: inline-block;
  color: #000;
  text-transform: uppercase;
  font-family: sans-serif;
  font-size: 8px;
  position: absolute;
  right: 10%;
  top: 20px;
}

.mobile__check::after {
  content: "⌵";
  display: block;
  text-align: center;
  font-size: 155%;
  font-weight: bold;
  position: relative;
}

.menu__btn {
  display: flex;
  align-items: center;
  position: absolute;
  left: 14px;
  top: 14px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  z-index: 5;
}

.menu__box {
  visibility: hidden;
  display: block;
  position: fixed;
  top: 49px;
  left: -100%;

  width: 100%;
  height: 715px;
  background-color: #f2f2f2;

  transition-duration: 0.5s;
}

.menu__item {
  display: flex;
  position: relative;
  color: #262626;
  font-size: 15px;
  width: 100vw;
}

.menu__link {
  display: block;
  position: relative;
  padding: 15px 24px 17px;
  border-bottom: 1px solid #e9e6e6;
  width: 100vw;
}

.menu__btn > span,
.menu__btn > span::after,
.menu__btn > span::before {
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: #262626;
  transition-duration: 0.5s;
}

.menu__btn > span::before {
  content: "";
  top: -5px;
}

.menu__btn > span::after {
  content: "";
  top: 5px;
}

.menu__btn--line,
.menu__btn--line::after,
.menu__btn--line::before {
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #262626;
  transition-duration: 0.5s;
}

.menu__btn--line::before {
  top: -14px;
  content: "";
}

.menu__btn--line::after {
  top: 14px;
  content: "";
}

.menu__btn:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}

.menu__toggle {
  display: none;
}

.menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
}

.menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
}

.menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}

.menu__toggle:checked ~ .menu__btn {
  position: fixed;
  left: 14px;
}

.menu__toggle:checked ~ .menu__box {
  visibility: visible;
  left: 0;
}

.mobile__logo--img {
  width: 116px;
  height: 44px;
  object-fit: contain;
}

.mobile__logo a:focus {
  border: 1px dashed black;
}

.mobile__icons {
  width: 16px;
  height: 16px;
  position: absolute;
  left: 60px;
  top: 18px;
}

.mobile__right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100px;
}

.mobile__left {
  width: 100px;
}

.nav {
  font-size: 13px;
  width: 51%;
  height: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.nav__link {
  color: #262626;
}

.nav__item {
  display: inline-block;
  margin-right: 15px;
}

.nav__button {
  background: #fff;
  border: 0;
}

.add {
  display: flex;
  align-items: center;
}

.add__account {
  width: 32px;
  height: 32px;
  border: 1px solid #9b9b9b;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8px;
}

.input-search {
  visibility: hidden;
}

.icons {
  margin-left: 5px;
}

.user-actions {
  width: 300px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.action {
  display: flex;
}

.more {
  display: none;
}

@media screen and (max-width: 1450px) {
  .description {
    display: none;
  }

  .nav {
    width: 100%;
  }

  .header__actions {
    justify-content: space-evenly;
  }

  .user-actions {
    width: 120px;
  }
}

@media screen and (max-width: 1100px) {
  .support {
    display: none;
  }

  .shop-now {
    display: none;
  }

  .more {
    display: inline-block;
  }
}

@media screen and (max-width: 910px) {
  .for-business {
    display: none;
  }
}

@media screen and (max-width: 770px) {
  .accessories {
    display: none;
  }
}

@media screen and (max-width: 859px) {
  .header__container {
    display: none;
  }

  .header__mobile {
    display: block;
  }
}
